import React from 'react';
import { Container, Grid, makeStyles, Theme } from '@material-ui/core';
import { Link } from 'gatsby';
import Image from 'gatsby-image';
import { generateLogger } from 'utils/loggers';
import { useLogoData } from 'hooks/useLogoData';
import DrawerMenu from './DrawerMenu';

const logger = generateLogger('global_header');
const handleLogoClick = () => {
  logger.click('click_logo');
};
const handleMenuClick = (name: string) => {
  logger.click(`click_menu`, { name });
};
const handleDrawerStateChange = (open: boolean) => {
  if (open) {
    logger.impression('open_global_menu');
  } else {
    logger.impression('close_global_menu');
  }
};

const useStyles = makeStyles<Theme>(({ spacing, palette, breakpoints, typography }) => ({
  root: {
    position: 'relative',
    backgroundColor: palette.background.paper,
  },
  drawerButton: {
    position: 'absolute',
    left: spacing(1),
    top: '50%',
    transform: 'translate(0, -50%)',
  },
  wrapper: {
    justifyContent: 'center',
    padding: `${spacing(2)}px 0`,
  },
  logo: {
    textAlign: 'center',
    marginTop: spacing(1),
  },
  list: {
    margin: 0,
    padding: 0,
  },
  item: {
    margin: `0 ${spacing(2)}px`,
    fontSize: typography.caption.fontSize,
    listStyle: 'none',
    [breakpoints.up('lg')]: {
      marginLeft: 0,
      marginRight: spacing(4),
    },
  },
  link: {
    textDecoration: 'none',
    color: 'inherit',
    '&.active': {
      color: palette.primary.main,
    },
    '&:visited': {
      color: 'inherit',
    },
  },
}));

const Header = () => {
  const styles = useStyles();
  const {
    logo: {
      childImageSharp: { fixed: logoUrl },
    },
  } = useLogoData();

  return (
    <Container component="header" maxWidth={false} className={styles.root}>
      <div className={styles.drawerButton}>
        <DrawerMenu onChange={handleDrawerStateChange} onClickMenu={handleMenuClick} />
      </div>
      <Grid container className={styles.wrapper} alignItems="center">
        <Grid item xs lg={'auto'} className={styles.logo}>
          <Link to="/" onClick={handleLogoClick}>
            <Image fixed={logoUrl} alt="로고" />
          </Link>
        </Grid>
        <Grid />
      </Grid>
    </Container>
  );
};

export default Header;
