import { useStaticQuery, graphql } from 'gatsby';
import { ImageSharp } from 'models/image';

interface LogoQueryResult {
  logo: ImageSharp;
}
export const useLogoData = () => {
  const data: LogoQueryResult = useStaticQuery(graphql`
    query {
      logo: file(absolutePath: { regex: "/logo-text.png/" }) {
        childImageSharp {
          fixed(width: 100, height: 24) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `);

  return data;
};
