import React, { useMemo } from 'react';
import { Grid, IconButton, Typography, makeStyles, Theme } from '@material-ui/core';
import { useProfileInfo } from 'hooks/useProfileInfo';
import Image from 'gatsby-image';
import * as Icon from 'react-feather';
import shape from '@material-ui/core/styles/shape';
import { generateLogger } from 'utils/loggers';

const logger = generateLogger('profile_card');
const handleLinkClick = (target: string) => () => {
  logger.click(`click_external_link`, { target });
};

const useStyles = makeStyles<Theme>(({ spacing, palette, breakpoints }) => ({
  root: {
    padding: spacing(2),
  },
  imageWrapper: {
    textAlign: 'center',
    '& img': {
      borderRadius: shape.borderRadius,
    },
    [breakpoints.up('lg')]: {
      marginRight: spacing(2),
    },
  },
  infoWrapper: {
    [breakpoints.up('lg')]: {
      marginRight: spacing(2),
    },
  },
  name: {
    color: palette.primary.main,
    textAlign: 'center',
    [breakpoints.up('lg')]: {
      textAlign: 'left',
    },
  },
  summary: {
    textAlign: 'center',
    marginBottom: spacing(2),
    [breakpoints.up('lg')]: {
      textAlign: 'left',
    },
  },
}));

const Profile = () => {
  const styles = useStyles();
  const {
    avatar: {
      childImageSharp: { fixed: avatar },
    },
    site: {
      siteMetadata: { author, social, description },
    },
  } = useProfileInfo();

  const socialData = useMemo(() => {
    return [
      {
        link: `mailto:${author.email}`,
        icon: <Icon.Mail />,
        name: 'email',
      },
      {
        link: social.github,
        icon: <Icon.GitHub />,
        name: 'github',
      },
      {
        link: social.linkedin,
        icon: <Icon.Linkedin />,
        name: 'linkedin',
      },
      {
        link: social.rss,
        icon: <Icon.Rss />,
        name: 'rss',
      },
    ];
  }, [social, author]);

  return (
    <Grid container className={styles.root} justify="center">
      <Grid item xs={12} md="auto" className={styles.imageWrapper}>
        <Image fixed={avatar} alt={author.name} />
      </Grid>
      <Grid item xs={12} md="auto" className={styles.infoWrapper}>
        <Typography variant="h6" className={styles.name}>
          {author.name}
        </Typography>
        <Typography gutterBottom variant="body2" className={styles.summary}>
          {author.summary}
        </Typography>
        <Typography gutterBottom variant="caption">
          {description}
        </Typography>
      </Grid>
      <Grid item>
        {socialData.map(({ icon, link, name }) => (
          <IconButton key={link} href={link} target="_blank" onClick={handleLinkClick(name)}>
            {icon}
          </IconButton>
        ))}
      </Grid>
    </Grid>
  );
};

export default Profile;
